class cars {

    constructor(carName = "Gen", spots = {}, icon, texture, textTex = {}) {
        this.carName = carName;
        this.carAnimations = {
            break: {

            },
            chais: {

            },
            battery: {

            },
        };
        this.carInfo = {
            first: null,
            second: null,
            third: null,
            vid1: null,
            vid2: null,
            vid3: null
        }
        this.carInfoCard = {
            first: null,
            second: null,
            third: null
        };
        this.carInfoCardTex = {
            eng: {
                first: null,
                second: null,
                third: null,
            },
            ar: {
                first: null,
                second: null,
                third: null,
            },
            vid1: null,
            vid2: null,
            vid3: null,
        };
        this.cancelCard = {
            first: null,
            second: null,
            third: null
        };;
        this.model = null;
        this.group = new THREE.Group();
        this.info = new THREE.Group();
        this.group.add(this.info);
        this.spots = spots;
        this.icon = icon;
        this.texture = texture;
        this.text = null;
        this.textTex = textTex;

        scene.add(this.group);
    }
}

