// creates i icon for all the cars 
function setupInfoButtons() {
    allSpot.forEach((i) => {
        for (let a = 0; a <= 2; a++) {
            let test = gensOrder[a];
            // console.log(test)
            carInfos[test].carInfo[i] = setInfo();
            carInfos[test].carInfo[i].name = test + " " + i
            carInfos[test].carInfo[i].position.x = carInfos[test].spots[i].x
            carInfos[test].carInfo[i].position.y = carInfos[test].spots[i].y
            carInfos[test].carInfo[i].position.z = carInfos[test].spots[i].z
            //
            // carInfos.Gen1.model.add()
            carInfos[test].info.add(carInfos[test].carInfo[i])
        }
        // console.log(carInfos.Gen1.info)

    })
    carInfos.Gen2.info.visible = false;
    carInfos.Gen3.info.visible = false;


}
setupInfoButtons();

function createInfoCard(index, spot) {
    let carName = gensOrder[index];
    let currentSpot = allSpot[spot];
    let naming = "vid" + (spot + 1);
    // console.log(naming)
    const mainGroup = new THREE.Group();
    const subMenu = new THREE.Group();
    const planeSet = new THREE.Mesh(
        new THREE.PlaneGeometry(2, 2),
        new THREE.MeshBasicMaterial({
            map: carInfos[carName].carInfoCardTex.eng[currentSpot],
            transparent: true,
            alphaTest: .5,
            side: THREE.DoubleSide,

        })
    )
    const planeSetAr = new THREE.Mesh(
        new THREE.PlaneGeometry(2, 2),
        new THREE.MeshBasicMaterial({
            map: carInfos[carName].carInfoCardTex.ar[currentSpot],
            transparent: true,
            alphaTest: .5,
            side: THREE.DoubleSide,

        })
    )
    // languageCar
    const planeSet1 = new THREE.Mesh(
        new THREE.PlaneGeometry(2, 2),
        new THREE.MeshBasicMaterial({
            map: blankTex,
            transparent: true,
            alphaTest: .5,
            side: THREE.DoubleSide,
            // depthTest: false


        })
    )
    const planeSet2 = new THREE.Mesh(
        new THREE.PlaneGeometry(1.8, 1.1),
        new THREE.MeshBasicMaterial({
            map: carInfos[carName].carInfoCardTex[naming],
            transparent: true,
            alphaTest: .5,
            side: THREE.DoubleSide,
            // depthTest: false


        })
    )
    const cancelBtn = new THREE.Mesh(
        new THREE.PlaneGeometry(1, 1),
        new THREE.MeshBasicMaterial({
            map: cancelTex,
            transparent: true,
            alphaTest: .5,
            side: THREE.DoubleSide,
            // depthTest: false


        })
    )
    carInfos[carName].carInfo[naming] = planeSet2;
    let direction = 1;
    if (spot == 1) {
        direction = -1
    } else {

    }

    cancelBtn.scale.set(.3, .3, .3)
    cancelBtn.position.set(.9, 1.7, 0.002)
    planeSet1.position.set(0, 1.146, 0)
    planeSet2.position.set(0, 1.146, 0.001)
    subMenu.position.set(direction, .6, 0)
    carInfos[carName].cancelCard[currentSpot] = cancelBtn;
    planeSetAr.visible = false;
    languageCar[carName].eng[currentSpot] = planeSet;
    languageCar[carName].ar[currentSpot] = planeSetAr;

    // console.log(carInfos[carName].cancelCard + " " + currentSpot)
    if (carName == "Gen2" && currentSpot == "first") {
        mainGroup.position.set(carInfos[carName].spots[currentSpot].x, carInfos[carName].spots[currentSpot].y + .3, carInfos[carName].spots[currentSpot].z - .05)
    } else {
        mainGroup.position.set(carInfos[carName].spots[currentSpot].x, carInfos[carName].spots[currentSpot].y, carInfos[carName].spots[currentSpot].z - .05)
    }
    subMenu.add(planeSet, planeSetAr, planeSet1, planeSet2, cancelBtn)
    mainGroup.add(subMenu)
    mainGroup.scale.set(0, 0, 1);
    mainGroup.visible = false
    carInfos[carName].carInfoCard[currentSpot] = mainGroup
    scene.add(carInfos[carName].carInfoCard[currentSpot])

}


// importing the models
function setUpModels(index, gltf) {
    let carName = gensOrder[index];
    const scenes = gltf.scene;

    let mat;
    if (carName == "Gen3") {
        scenes.traverse(function (object) {

            mat = object.material

        });
        mat.map = carInfos[carName].texture;
        mat.transparent = true
        // mat = new THREE.MeshPhysicalMaterial({
        //     map: carInfos[carName].texture,
        //     normalMap: gen3Normal,
        //     side: THREE.DoubleSide,
        //     transparent: true,

        //     metalness: .4,
        //     envMap: envTex,
        //     envMapIntensity: 2
        // });
    } else {
        mat = new THREE.MeshStandardMaterial({

            side: THREE.DoubleSide,
            map: carInfos[carName].texture,
            transparent: true,

        });
    }

    mat.skinning = true

    // mat.envMap = envTex;
    mat.needsUpdate = true;
    scenes.traverse(function (object) {

        object.material = mat

    });

    carInfos[carName].model = gltf.scene;


    // seting the intial position
    if (index == 0) {
        carInfos[carName].model.position.y = -.7;
        carInfos[carName].model.position.z = 1;
        carInfos[carName].model.rotation.set(0, -d2R(90), 0)
        carInfos[carName].model.position.x = 0;
    } else {
        carInfos[carName].model.position.y = -1.6;
        carInfos[carName].model.position.z = -1;

        carInfos[carName].model.scale.set(.7, .7, .7)
    }
    if (carName == "Gen3") {
        carInfos[carName].model.position.z = -1.2;

    }

    carInfos[carName].text = setText(index)


    if (index == 0) {
        carInfos[carName].text.rotation.set(0, d2R(90), 0)
        carInfos[carName].text.position.y = 2;
        carInfos[carName].text.position.x = -1;

    } else {
        carInfos[carName].text.position.y = 4;
        carInfos[carName].text.scale.set(1.3, 1.3, 1.3);
        carInfos[carName].text.position.z = 1.3;
        carInfos[carName].model.visible = false;
        carInfos[carName].model.position.x = 5;
    }
    //  carInfos[carName].text

    carInfos[carName].model.add(carInfos[carName].text)
    carInfos[carName].group.add(carInfos[carName].model)

}

