// Animations

function playReverse(carNo, index) {

    let carName = gensOrder[carNo];
    let animName = animOrder[index];
    let currentSpot = allSpot[index];


    gsap.to(carInfos[carName].carInfoCard[currentSpot].scale, {
        duration: .3,
        x: 0,
        y: 0,
        onComplete: () => {
            carInfos[carName].carInfoCard[currentSpot].visible = true;
        }

    })
    carInfos[carName].text.material.opacity = 1;
    carInfos[carName].text.visible = true;
    carInfos[carName].carAnimations[animName].reset();

}

function resetAnim() {
    carClose = dumyCar;
    playReverse(currentCar, 0);
    playReverse(currentCar, 1);
    playReverse(currentCar, 2);
}

function playAnim(index) {
    let carNo = currentCar;
    let carName = gensOrder[carNo];
    let animName = animOrder[index];
    let currentSpot = allSpot[index];

    playReverse(currentCar, 0);
    playReverse(currentCar, 1);
    playReverse(currentCar, 2);
    carClose = carInfos[carName].cancelCard[currentSpot];
    carInfos[carName].carInfoCard[currentSpot].visible = true;
    gsap.to(carInfos[carName].carInfoCard[currentSpot].scale, {
        duration: 1,
        x: 1,
        y: 1,

    })
    if (index == 1) {
        let textMat1 = carInfos[carName].text;
        gsap.to(textMat1.material, {
            duration: .5,
            opacity: 0,
            delay: 0.2,
            onComplete: () => {
                carInfos[carName].text.visible = false;
            }
        })
    }
    carInfos[carName].carInfoCard[currentSpot]
    carInfos[carName].carAnimations[animName].play()
}


function menuSetup() {
    if (currentCar == 0) {
        centerIcon.material.map = icon1;
        nextArrow.visible = true;
        beforeArrow.visible = false;

    } else if (currentCar == 1) {
        centerIcon.material.map = icon2;

        nextArrow.visible = true;
        beforeArrow.visible = true;

    } else {
        centerIcon.material.map = icon3;

        nextArrow.visible = false;
        beforeArrow.visible = true;

    }
}
menuSetup();

// Moves the car and fades the mat
function moveCarInDir(obj1, obj2, textMat1, textMat2, dir) {
    obj1.visible = true;
    obj2.material.opacity = 0;
    obj2.visible = true;
    let side = 0;

    if (dir == "before") {
        side = 5;
    } else {
        side = -5;
    }
    gsap.to(obj1.position, {
        duration: 1,
        x: obj1.position.x + side,
        delay: 0,

    })
    gsap.to(obj1.material, {
        duration: .5,
        opacity: 0,
        delay: 0.2,
    })
    gsap.to(textMat1.material, {
        duration: .5,
        opacity: 0,
        delay: 0.2,
    })
    gsap.to(obj2.material, {
        duration: .5,
        opacity: 1,
        delay: 0,

    })
    gsap.to(textMat2.material, {
        duration: .5,
        opacity: 1,
        delay: 0,

    })
    gsap.to(obj2.position, {
        duration: 1,
        x: obj2.position.x + side,
        delay: 0,
        onComplete: () => {
            let carName = gensOrder[currentCar];

            carInfos[carName].info.visible = true;
        }
    })
}

// Based on direction and the current car the cars are selected to move
function moveCar(dir) {
    resetAnim();

    setTimeout(() => {

        carInfos.Gen1.info.visible = false;
        carInfos.Gen2.info.visible = false;
        carInfos.Gen3.info.visible = false;
        if (currentCar == 0 && dir == "next") {

            moveCarInDir(carInfos.Gen1.model, carInfos.Gen2.model, carInfos.Gen1.text, carInfos.Gen2.text, dir)
            currentCar = 1;
        } else if (currentCar == 1 && dir == "next") {


            moveCarInDir(carInfos.Gen2.model, carInfos.Gen3.model, carInfos.Gen2.text, carInfos.Gen3.text, dir)
            currentCar = 2;
        } else if (currentCar == 2 && dir == "before") {
            moveCarInDir(carInfos.Gen3.model, carInfos.Gen2.model, carInfos.Gen3.text, carInfos.Gen2.text, dir)
            currentCar = 1;
        } else if (currentCar == 1 && dir == "before") {
            moveCarInDir(carInfos.Gen2.model, carInfos.Gen1.model, carInfos.Gen2.text, carInfos.Gen1.text, dir)
            currentCar = 0;

        }

        menuSetup();
    }, 500)

}

// Animation for the arrow and the car movement
function arrowAnim(obj) {

    if (obj == "before") {
        gsap.to(beforeArrow.position, {
            duration: .3,
            x: beforeArrow.position.x - .1,
            delay: 0,
            yoyo: true,
            repeat: 1,
            onComplete: moveCar(obj)
        })
    } else {
        gsap.to(nextArrow.position, {
            duration: .3,
            x: nextArrow.position.x + .1,
            delay: 0,
            yoyo: true,
            repeat: 1,
            onComplete: moveCar(obj)
        })
    }



}

// Moves the camera to the active spot
function moveCameraToslot(spot) {
    let carName = gensOrder[currentCar];
    let spotsOffset = ["firstOffset", "secondOffset", "thirdOffset"];

    let spots = allSpot[spot];
    playAnim(spot)


    gsap.to(camera.position, {
        duration: 1,
        x: carInfos[carName].spots[spots].x + carInfos[carName].spots[spotsOffset[spot]].x,
        y: carInfos[carName].spots[spots].y + carInfos[carName].spots[spotsOffset[spot]].y,
        z: carInfos[carName].spots[spots].z + carInfos[carName].spots[spotsOffset[spot]].z,
        delay: 0
    });

}



